.node {
  width: 17px;
  height: 17px;
  outline: 1px solid rgb(175, 216, 248);
  display: inline-block;
  margin-left: 2px;
  margin-right: 2px;
}

.node-finish {
  background-color: red;
  background-image: url(./images/flag.jpg);
  background-size: cover;
  box-shadow: 2px 4px 20px 7px rgb(255, 0, 0);
  outline: 1px solid rgb(255, 0, 0);
}

.node-start {
  background-color: green;
  background-image: url(./images/astronaut.jpg);
  background-size: cover;
  box-shadow: 2px 4px 20px 7px rgb(9, 255, 0);
  outline: 1px solid rgb(9, 255, 0);
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(44, 132, 247, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(60, 219, 243, 0.75);
  }
}

.node-wall {
  /* background-color: grey; */
  background-image: url(./images/brick_wall.jpg);
  background-size: cover;
  outline: 2px solid black;
}
.node-wallweight {
  /* background-color: orange; */
  background-image: url(./images/weighted_wall.jpg);
  background-size: cover;
}
.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
